import React, { useEffect, useState, useCallback } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useJsApiLoader, GoogleMap, Marker } from "@react-google-maps/api";
import { myPositionActions, parkingActions } from "../store/store";

// 
import SearchBar from "../components/SearchBar";
import Filter from "../components/Filter";
import Information from "../components/Information";
import Position from "../components/Position";
import IsLoaded from "../components/IsLoaded";

// 
import mapStyles from "../helpers/mapStyles";
import centerLatLngOfDistrict from "../helpers/centerLatLngOfDistrict";
import key from "../key";

// Marker 
import markerIcon from "../assets/images/marker.svg";
import markerZeroIcon from "../assets/images/markerZero.svg";
import clickedMarkerZero from "../assets/images/clickedMarkerZero.svg";

const libraries = ["places"];

function Parking() {
  const dispatch = useDispatch();

  //  state 
  const [userLatLng, setUserLatLng] = useState(null);

  const [map, setMap] = useState(null);
  const [data, setData] = useState([]);
  const [center, setCenter] = useState({ lat: 22.9999, lng: 120.227 }); // 

  // Redux 
  const myPosition = useSelector((state) => state.myPosition.myPosition);  // 
  const info = useSelector((state) => state.parking.info);                // 
  const dataId = useSelector((state) => state.parking.dataId);            //  ID
  const district = useSelector((state) => state.filter.district);         // 
  const showFilter = useSelector((state) => state.filter.showFilter);     //  Filter
  const searchLatLng = useSelector((state) => state.search.searchLatLng); // 

  // Google Maps API
  const { isLoaded } = useJsApiLoader({
    googleMapsApiKey: key(),
    libraries,
    language: "zh-TW",
    region: "TW",
  });

  //  /public/parking.json 
  useEffect(() => {
    const fetchParkingData = async () => {
      try {
        const response = await fetch(
          "https://piin0112.github.io/parking/parking.json"
        );
        const parkingData = await response.json();
        const parsedData = parkingData.map((item) => ({
          ...item,
          LatLng: {
            lat: parseFloat(item.lnglat.split(",")[0]),
            lng: parseFloat(item.lnglat.split(",")[1]),
          },
        }));
        setData(parsedData);
      } catch (error) {
        console.error("", error);
      }
    };
    fetchParkingData();
  }, []);

  // 
  useEffect(() => {
    if (!map) return;
    if (district) {
      const found = centerLatLngOfDistrict.find((d) => d.district === district);
      if (found) {
        map.panTo({ lat: found.lat, lng: found.lng });
      }
    }
  }, [district, map]);

  // 
  const handleDragEnd = useCallback(() => {
    if (!map) return;
    const newCenter = map.getCenter();
    if (!newCenter) return;
    const lat = newCenter.lat();
    const lng = newCenter.lng();
    setCenter({ lat, lng });
  }, [map]);

  //  ( userLatLng )
  useEffect(() => {
    if (myPosition && map) {
      navigator.geolocation.getCurrentPosition(
        (pos) => {
          const currentLatLng = {
            lat: pos.coords.latitude,
            lng: pos.coords.longitude,
          };
          //  state
          setUserLatLng(currentLatLng);
          map.panTo(currentLatLng);
          setCenter(currentLatLng);
        },
        (err) => {
          console.error("", err);
        }
      );
      dispatch(myPositionActions.notMyPosition());
    }
  }, [myPosition, map, dispatch]);

  // 
  useEffect(() => {
    if (!map) return;
    if (searchLatLng && searchLatLng.lat && searchLatLng.lng) {
      map.panTo(searchLatLng);
      setCenter(searchLatLng);
    }
  }, [searchLatLng, map]);

  // 
  useEffect(() => {
    if (!map || !dataId) return;
    const target = data.find((item) => item.id === dataId);
    if (target) {
      dispatch(parkingActions.getInfo(target));
      map.panTo(target.LatLng);
    }
  }, [dataId, data, map, dispatch]);

  //  Loading 
  if (!isLoaded) {
    return <IsLoaded />;
  }

  return (
    <div className="park">
      {!info?.id && <Position />}
      {showFilter && <Filter />}
      <SearchBar />
      {info?.id && <Information data={info} />}

      <GoogleMap
        center={center}
        zoom={15}
        mapContainerStyle={{ width: "100%", height: "100%" }}
        mapContainerClassName="map-container"
        options={{
          styles: mapStyles,
          streetViewControl: false,
          mapTypeControl: false,
          fullscreenControl: false,
          gestureHandling: "greedy", // 
        }}
        
        onLoad={(mapInstance) => setMap(mapInstance)}
        onDragEnd={handleDragEnd}
      >
        
        {/*  Marker */}
        {userLatLng && (
          <Marker
            position={userLatLng}
            icon={{
              url: "http://maps.google.com/mapfiles/ms/icons/blue-dot.png", //  Marker
              scaledSize: new window.google.maps.Size(40, 40),
            }}
          />
        )}

        {/*  Marker */}
        {data.map((park) => {
          if (info?.id === park.id) {
            return (
              <Marker
                key={park.id}
                position={park.LatLng}
                icon={{
                  url: park.car > 0 ? clickedMarkerZero : clickedMarkerZero,
                }}
                label={String(park.car || 0)}
                onClick={() => dispatch(parkingActions.getDataId(park.id))}
              />
            );
          }
          return (
            <Marker
              key={park.id}
              position={park.LatLng}
              icon={{
                url: park.car > 0 ? markerIcon : markerZeroIcon,
              }}
              label={String(park.car || 0)}
              onClick={() => dispatch(parkingActions.getDataId(park.id))}
            />
          );
        })}
      </GoogleMap>
    </div>
  );
}

export default Parking;
