import Icons from "../assets/images/icons"
import Position from "./../components/Position"

function Information({data}) {
  return (
    <div className="info__card">
      <div className="info__position">
        <Position />
      </div>
      <div className="info__container">
        <div className="info__dragger">
        </div>
        <div className="info__above">
          <div className="info__above-content">
            <div className="info__above-left">
              <h3>{data.name}</h3>
              <p> {data.car_total}  {data.car}</p>
              <p>{data.chargeFee}</p>
            </div>
            <a href={`https://www.google.com/maps/dir/?api=1&destination=${data.name},${data.address !== ""? data.address:""}` }className="info__above-right" >
              <Icons.SVGNavigator />
              <div style={{color: "#192851"}}></div>
            </a>
          </div>
        </div>
        <hr/>
        <div className="info__below">
          <div className="info__below-detail">
            <p> {data.chargeTime}</p>
            <p> {data.typeName}</p>
            <p> {data.zone} </p>
            <p> {data.address} </p>
          </div>
        </div>
      </div>
    </div>
  );
}


export default Information
